import { FaInstagram,FaGithub,FaLinkedin } from "react-icons/fa";
import classes from './Contact.module.css'

const handleURL= (url) =>
{
    return () => window.open(url, "_blank")
}


const Contact = () => {
    return ( 
            <div className={classes.Contact}id="Contact">
                <div className={classes.icons}>
            < FaGithub color='white' size='30px'style={{padding : '1%'}}
            onClick={handleURL('https://github.com/prateekprashar1998')} />
            <FaLinkedin color='white' size='30px'style={{padding : '1%'}}
            onClick={handleURL('https://www.linkedin.com/in/prateek-prashar20/')} />
            <FaInstagram color='white' size='30px'style={{padding : '1%'}}
            onClick={handleURL('https://www.instagram.com/prashar__prateek/')}/>
      </div>
      </div>
     );
}
 
export default Contact;