import classes from './About.module.css'
import Pageheader from './../Pageheader/Pageheader'
import Mem from './../../img/Mem.jpg'
const About = () => {
    return ( 
        <div className={classes.AboutMe}id="About">
            <Pageheader title={'About Me'} />
            <div className={classes.Container} >
               <div className={classes.Text} >
                  <h2> Hello! I'm Prateek</h2>
                    <h4>
                    I'm a Software developement enthusiast based in India.
                    As a person, I am quite capable of solving critical problems by coming up with creative solutions. I am extremely passionate about the work that I do.
                    I also strongly believe in changing the world with ground-breaking ideas.
                    </h4>
                 </div>
        <div className={classes.Photo}>
            <img className={classes.Mem} src={Mem} alt="mem"></img>
        </div>
    </div>
   </div>

    
    
    );
}
 
export default About;