import classes from './Skills.module.css'
import Pageheader from '../Pageheader/Pageheader';
import codethinking from './../../img/codethinking.svg'
const listTitleStyle={ fontWeight: 900, color :'#9B1FE8',marginBottom:'4px'}
const backendSkills =
<ul>
  <li style={listTitleStyle} >BACKEND :</li>
  <li>Python-Java-SQL-Nodejs</li>
  <li>MongoDb-MySQL-Firebase</li>
</ul>
const frontendSkills =
<ul>
  <li style={listTitleStyle}>FRONTEND :</li>
  <li>Reactjs-CSS-HTML</li>
</ul>
const DataScienceSkills =
<ul>
  <li style={listTitleStyle} >Data Analysis :</li>
  <li>Python(Numpy-Matplotlib-Pandas)</li>
</ul>
const OtherSkills =
<ul>
  <li style={listTitleStyle}>Other :</li>
  <li>Git-Github-Tensorflow</li>
</ul>
const totalSkills =[frontendSkills,backendSkills,DataScienceSkills,OtherSkills]


  const Skills = () => {
    return ( 
      <div className={classes.Skills}id="Skills">
        
          <Pageheader title={'What about the skills?'} />
          <h4>
          I'm a life long learner and enjoy leaning new stuff! Below are some programming languages, frameworks and libraries that I worked with and like. Some of them I control better than the other.
          I can bring new ideas,Learning new technologies and implementing them to enhance the existing functionalities.

          </h4>
          <div className={classes.Container}>
           
            <img src={codethinking} alt="codethinking"/>
            {totalSkills.map(skills => {
               return(
                 <div className={classes.List}>
                   {skills}
                   </div>
                   

               )
            })}
          </div>
      </div>
     );
}
 
export default Skills;
