import classes from './YouTube.module.css'
import Pageheader from '../Pageheader/Pageheader'
import cleancode from './../../img/cleancode.jpg'
import JavaStreams from './../../img/JavaStreams.jpg'
import Pythonlambdas from './../../img/Pythonlambdas.png'
import universe from './../../img/universe.jpg'
const playBtnSvg = (url) =>{
    return(
        <div className={classes.PlayButton}
        onClick={(e) => window.open(url," _blank").focus}
        >
        <svg xmlns="http://www.w3.org/2000/svg" width="96" height="96" viewBox="0 0 24 24"><path d="M19.615 3.184c-3.604-.246-11.631-.245-15.23 0-3.897.266-4.356 2.62-4.385 8.816.029 6.185.484 8.549 4.385 8.816 3.6.245 11.626.246 15.23 0 3.897-.266 4.356-2.62 4.385-8.816-.029-6.185-.484-8.549-4.385-8.816zm-10.615 12.816v-8l8 3.993-8 4.007z"/></svg>
        </div>

    );
}
const youtubeVideo = (url, thumbnail, description) =>
{
    return(
        <div className={classes.VideoContainer}>
            <div className={classes.Thumbnail}>
                {playBtnSvg(url)
                }
                <img src={thumbnail} alt="thumbnail"></img>
            </div>
            <div className={classes.Text}>
                <h7>{description}</h7>
            </div>
            </div>
    )
}
const cleancodeVideoDescription = 'We will Learn how to write readable code. In Most of them are based on the book Clean Code by Robert C. Martin'

const JavaStreamsVideoDescription = 'Java Streams are introduced to Java in a functional style. In this video we explain how Java Streams work'

const PythonLambdasDescription = 'In this video we dive into Python lambdas functional programming. We also explain about higher-order functions and lambdas'

const  YouTube = ()  => 
{

     return( 
        <div className={classes.YouTube}id="YouTube">
            <img className={classes.Background}src={universe} alt="Background" />
        <Pageheader title={"I'm also on YouTube"}/>
        <div className={classes.YouTubeContent}>
            <div className={classes.Paragraph}>
                <h7>
                On my channel you will find videos about programming and related stuff. Since that's what I like. Check out some of my latests videos. If you like what you see then please subscribe.
                </h7>
            </div>
            <div className={classes.Videos}>
              {youtubeVideo(" ",cleancode,cleancodeVideoDescription)}
              {youtubeVideo(" ",JavaStreams,JavaStreamsVideoDescription)}
              {youtubeVideo(" ",Pythonlambdas,PythonLambdasDescription)}

            </div>
        </div>
      </div>
     );
      
    }
export default YouTube;